import React, { useState } from 'react';
import Calendar from 'react-calendar';
import 'react-calendar/dist/Calendar.css';  //  CSS 

function CalendarComponent() {
  //  state
  const [tasks, setTasks] = useState([]);  // 
  const [workdays, setWorkdays] = useState([]);  // 
  const [newTask, setNewTask] = useState('');  // 
  const [selectedDate, setSelectedDate] = useState(null);  // 

  // 
  const toggleTaskStatus = (taskDate) => {
    setTasks(tasks.map((task) =>
      task.date === taskDate 
        ? { ...task, status: task.status === '' ? '' : '' }
        : task
    ));
  };

  // 
  const handleWorkdaySelection = (date) => {
    const dateString = date.toDateString();
    if (workdays.includes(dateString)) {
      setWorkdays(workdays.filter((day) => day !== dateString));
    } else {
      setWorkdays([...workdays, dateString]);
    }
  };

  // 
  const handleTaskInput = (e) => {
    setNewTask(e.target.value);
  };

  // 
  const renderTasksForDate = (date) => {
    const taskForDay = tasks.filter((task) => task.date === date.toDateString());
    return taskForDay.length > 0 ? taskForDay[0].task : '';
  };

  return (
    <div className="calendar-container">
      <h2></h2>

      {/*  */}
      <div className="workday-section">
        <h3></h3>
        {/*  */}
        <Calendar
          onClickDay={(date) => {
            setSelectedDate(date);  // 
            handleWorkdaySelection(date);  // 
          }}
          tileClassName={({ date }) => 
            workdays.includes(date.toDateString()) ? 'workday' : null
          }
        />
      </div>

      {/*  */}
      <div className="todo-section">
        <h3></h3>
        {/*  */}
        <div>
          <input
            type="text"
            value={newTask}
            onChange={handleTaskInput}
            placeholder=""
          />
          <button onClick={() => {
            if (selectedDate && newTask) {
              setTasks([
                ...tasks,
                { date: selectedDate.toDateString(), task: newTask, status: '' }
              ]);
              setNewTask('');  // 
            }
          }}>
            
          </button>
        </div>

        {/*  */}
        <table>
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {tasks.map((task, index) => (
              <tr key={index}>
                <td>{task.date}</td>
                <td>{task.task}</td>
                <td>
                  {/*  */}
                  <button 
                    onClick={() => toggleTaskStatus(task.date)}
                    style={{
                      color: task.status === '' ? 'green' : 'red',
                      background: 'none',
                      border: 'none',
                      cursor: 'pointer'
                    }}
                  >
                    {task.status}
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
}

export default CalendarComponent;
