import React, { useState } from 'react';
import { BarChart, Bar, XAxis, YAxis, Tooltip, Legend, CartesianGrid } from 'recharts';

function FinanceManager() {
  const [income, setIncome] = useState(0); // 
  const [expense, setExpense] = useState(0); // 
  const [salary, setSalary] = useState(0); // 
  const [bonus, setBonus] = useState(0); // 
  const [laborInsurance, setLaborInsurance] = useState(0); // 
  const [healthInsurance, setHealthInsurance] = useState(0); // 
  const [netIncome, setNetIncome] = useState(0); // 

  // 
  const LABOR_INSURANCE_RATE = 0.115; //  11.5%
  const EMPLOYEE_LABOR_CONTRIBUTION = 0.2; //  20%
  const HEALTH_INSURANCE_RATE = 0.0517; //  5.17%
  const EMPLOYEE_HEALTH_CONTRIBUTION = 0.3; //  30%

  // 
  const handleCalculate = () => {
    const incomeValue = parseFloat(income) || 0;
    const expenseValue = parseFloat(expense) || 0;
    const salaryValue = parseFloat(salary) || 0;

    //  15%
    const calculatedBonus = salaryValue * 0.15;

    // 
    const calculatedLaborInsurance = salaryValue * LABOR_INSURANCE_RATE * EMPLOYEE_LABOR_CONTRIBUTION;

    // 
    const calculatedHealthInsurance = salaryValue * HEALTH_INSURANCE_RATE * EMPLOYEE_HEALTH_CONTRIBUTION;

    // 
    const calculatedNetIncome = incomeValue - expenseValue + salaryValue + calculatedBonus - calculatedLaborInsurance - calculatedHealthInsurance;

    // 
    setBonus(calculatedBonus);
    setLaborInsurance(calculatedLaborInsurance);
    setHealthInsurance(calculatedHealthInsurance);
    setNetIncome(calculatedNetIncome);
  };

  // 
  const data = [
    { name: '', value: parseFloat(income) || 0 },
    { name: '', value: parseFloat(expense) || 0 },
    { name: '', value: parseFloat(salary) || 0 },
    { name: '', value: bonus },
    { name: '', value: laborInsurance },
    { name: '', value: healthInsurance },
    { name: '', value: netIncome }
  ];

  return (
    <div>
      <h2></h2>

      {/*  */}
      <div>
        <label>
          :
          <input type="number" placeholder="" onChange={(e) => setIncome(e.target.value)} />
        </label>
      </div>

      {/*  */}
      <div>
        <label>
          :
          <input type="number" placeholder="" onChange={(e) => setExpense(e.target.value)} />
        </label>
      </div>

      {/*  */}
      <div>
        <label>
          :
          <input type="number" placeholder="" onChange={(e) => setSalary(e.target.value)} />
        </label>
      </div>

      {/*  */}
      <button onClick={handleCalculate}></button>

      {/*  */}
      <div>
        <h3></h3>
        <p>: {bonus.toFixed(2)}</p>
        <p>: {laborInsurance.toFixed(2)}</p>
        <p>: {healthInsurance.toFixed(2)}</p>
        <p>: {netIncome.toFixed(2)}</p>
      </div>

      {/*  */}
      <BarChart width={600} height={400} data={data}>
        <CartesianGrid strokeDasharray="3 3" />
        <XAxis dataKey="name" />
        <YAxis />
        <Tooltip />
        <Legend />
        <Bar dataKey="value" fill="#8884d8" />
      </BarChart>
    </div>
  );
}

export default FinanceManager;
